@import url(https://fonts.googleapis.com/css?family=Rubik:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

$bg: #34495e;
$primarycolor: #f39c12;
$white: #ecf0f1;
$font: 'Rubik';

html, body {
  background: radial-gradient($bg 0, darken($bg, 15%) 100%);
  min-height: 100vh;
  font-family: $font, sans-serif;
  padding: 0;
  margin: 0;
}

.description {
  color: $white;
  font-size: 22px;
  line-height: 1.4;
  padding: 50px 0;
  text-align: center;
  width: 100%;
  
  a,
  a:hover,
  a:active,
  a:focus,
  a:visited {
    color: darken($white, 15%);
    text-decoration: none;
  }
  
  a:hover {
    border-bottom: 1px solid lighten($white, 15%);
  }
}

.menu-container {
  box-shadow: 0 0px 50px hsla(0, 0, 0, 0.7);
  position: relative;
  -webkit-transform: translate(0);
          transform: translate(0);
  margin: 0 auto;
  width: 350px;
}

.menu-item,
.menu-item--active {
  height: 60px;
  width: 100%;
}

.menu-item {
  align-items: left;
  color: $white;
  cursor: pointer;
  display: flex;
  font-size: 24px;
  justify-content: left;
  letter-spacing: 4px;
  text-align: center;
  
  &.active {
    box-shadow: 0 2px 3px hsla(0, 0, 0, 0.7);
  }
 
  &:nth-of-type(1) {
    background: $primarycolor;
  }
  &:nth-of-type(2) {
    background: darken($primarycolor, 5%);
  }
  &:nth-of-type(3) {
    background: darken($primarycolor, 10%);
  }
  &:nth-of-type(4) {
    background: darken($primarycolor, 15%);
  }
  &:nth-of-type(5) {
    background: darken($primarycolor, 20%);
  }
}

.menu-item--active {
  background: $primarycolor;
  border-radius: 2px !important;
  box-shadow: 0 0px 50px hsla(0, 0, 0, 0.5);
  left: 50%;
  padding: 0 7px;
  position: absolute;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-transition: all 300ms;
  transition: all 300ms;
  z-index: -1;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

